<template>
  <div class="container">
    <form @submit="preventSubmit">
      <div class="form-group">
        <label>请在此输入您想说的话：</label>
        <textarea class="form-control" v-model="content" placeholder="评论长度不能大于200也不能为空"></textarea>
        <span v-show="flag2" style="color: red;font-weight: 700">{{errMsg2}}</span>
      </div>
      <button class="btn btn-default" @click="addLetter" :disabled="!isLogin">发表评论</button>
      <button class="btn btn-default" @click="getLetters">查看评论</button>
    </form>
    <!-- 展示评论 -->
    <div v-if="flag3">
      <table class="table table-striped">
        <thead>
          <tr>
            <th>name</th>
            <th>content</th>
            <th>time</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in comments" :key="index">
            <td>{{item.annoyname}}</td>
            <td>{{item.lettercontent}}</td>
            <td>{{item.ettertime}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Comments',
  data () {
    return {
      // isLogin: false,
      flag1: false,
      flag2: false,
      flag3: false,
      errMsg1: "请输入昵称",
      errMsg2: "请输入内容",
      content: '', // 内容
      time: '', // 时间
      comments: [], // 评论集合
    }
  },
  props: {
    msg: String,
    // isLogin:Boolean
  },
  methods: {
    // 判断名称以及内容是否为空
    judgeNull: function () {
      if (!this.content) {
        this.flag2 = true;
      }
    },

    // 获取当前时间
    getTime: function () {
      this.time = new Date;
      let year = this.time.getFullYear();
      let month = this.time.getMonth() + 1;
      let day = this.time.getDate();
      this.time = year + '.' + month + '.' + day;
      console.log(this.time);
    },

    getLetters: function () {

      // this.$axios.get('/api/getLetters')
      //   .then(response => (this.comments = response.data))
      //   .catch(function (error) {
      //     console.log("获取失败!");
      //   })
      this.flag3 = true;
    },

    addLetter: function () {
      console.log(this.$store.state.userName);
      console.log(this.content);
      // 添加评论
      this.getTime();
      this.judgeNull();
      if (this.content) {
        this.$axios.post('/api/addLetter', {
          username: this.$store.state.userName,
          lettercontent: this.content,
          lettertime: this.time
        })
          .then(function (response) {
            console.log(response);
            alert("发表成功");
          })
          .catch(function (error) {
            console.log(error);
          })
        this.userName = '';
        this.content = '';
        
      }
    },
    preventSubmit: function (e) {
      e.preventDefault();
    }
  },
  mounted () {

    this.$axios.get('/api/getAllLettersByurl')
      .then((response) => {
        this.comments = response.data;
        console.log(response);
      })
      .catch(function (error) {
        console.log("获取失败!");
      })
  },
  computed: {
    isLogin () {
      return this.$store.state.isLogin;
    }
  },
}
</script>

